import {DebugActionType, OMS} from "@/OMS";
import {TemplateElement} from "@/template/TemplateElement";
import {BrowserUtils} from "@/BrowserUtils";
import {Auth} from "@/api/auth/Auth";
import {User} from "@/api/auth/User";
import {Template, TemplateState} from "@/template/Template";
import {TemplateHead} from "@/template/TemplateHead";
import {UserRole} from "@/api/auth/UserRole";
import {DefaultData} from "@/demo/DefaultData";

@Component({
  name: "ConsoleNavbar",
  components: {}
})

export default class ConsoleNavbar extends Vue {

  get body() { return OMS.getTemplate() }

  get template(): Template { return OMS.getTemplate() }
  get loggedUser(): User { return OMS.getLoggedUser(); }

  get isDebugMode(): boolean { return BrowserUtils.debugMode; }

  isModalActive = false;

  status = {
    login: false
  }

  mounted() {
    console.log(`%c[UI] 'Nav' has been spawned!`, 'color: #bada55');
    //OMS.getInstance().$store.state.builderSettings.loginPopup = true;
  }

