    OMS.setDebugMode(DebugActionType.TOGGLE);
  }

  changeStyle() {
    OMS.setCustomStyleSource('https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css');
  }

  setTemplate() {
    OMS.setTemplate(new Template(
        1,
        'ดราฟไว้ก่อนไว้ค่อยทำต่อ',
        'version 0.1 ยังไม่เสร็จ',
        new TemplateHead(
            'SAMT',
            'https://www.mysamt.com/assets/favicon/favicon-96x96.png',
            [
              {
                name: 'msapplication-TileColor',
                content: '#ffffff'
              },
              {
                name: 'theme-color',
                content: '#ffffff'
              },
              {
                name: 'description',
                content: 'หางานสอนดนตรี, กีฬา ศิลปะ, เทคโนโลยี่ อันดับ 1 สมัครฟรี! เหมาะสำหรับคุณครูที่ต้องการอิสระในการรับงาน หรือ ต้องการขยายฐานลูกค้าแบบมืออาชีพ'
              },
            ],
