        ),
        new DefaultData().initBodyFromJSON(),
        'body: {background-color: orange}',
        new User(
            1,
            'gamelovemiku',
            '6014110007@mutacth.com',
            'Sittipon',
            'Khayemka',
            'https://pbs.twimg.com/profile_images/1109911712201736192/O65BBJT-_400x400.png',
            UserRole.DESIGNER,
            'a8d4s654bvjuro5v58r5bi0e'
        ),
